.table {
  width: 100%;
  height: calc(100% - 5rem);
  margin-top: 1rem;
  /* border: 1px solid red; */
  display: flex;
  flex-direction: row;
  font-weight: normal;
  position: relative;

  .Header {
    position: inherit;
    background-color: rgba(0, 0, 0, 0.4);
    color: rgb(255, 255, 255);
    padding-left: 1rem;
    font-weight: 600;
    font-size: 1.2rem;
    width: calc(100% - 1rem);
    border-radius: .5rem .5rem 0 0;
  }

  .FirstRow {
    position: relative;
    color: white;
    font-weight: normal;
    font-size: 1.5rem;
    width: 100%;

    .Column {
      height: calc(50% - 2.1rem);
      font-size: 1.2rem;
      width: 100%;
      overflow: auto;
      display: flex;
      background-color: rgba(100, 100, 100, 0.3);
      margin: 0 0 .5rem 0;
      border-radius: .5rem;
      border-radius: 0 0 .5rem .5rem;
      flex-direction: column;

      .ContentRow {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding-left: .4rem;
      }
    }
  }

  .SecondRow {
    position: relative;
    color: white;
    font-weight: normal;
    font-size: 1.2rem;
    width: 100%;

    .Column {
      height: 100%;
      font-size: 1.2rem;
      width: calc(100% - .5rem);
      overflow: auto;
      display: flex;
      background-color: rgba(100, 100, 100, 0.3);
      margin: 0 0 0 .5rem;
      border-radius: .5rem;
      flex-direction: column;

      .ContentRow {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding-left: .4rem;
      }
    }
  }
}