.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;

  .header {
    display: flex;
    flex-direction: row;
    /* align-items: center; */
    justify-content: space-between;
    width: 100%;
    height: 10%;

    .parkingName {
      display: flex;
      width: 100%;
      margin: 1.5rem 1.5rem;
      color: white;
      font-size: 1.5rem;
      justify-content: flex-start;
      align-items: center;
      flex-direction: row;

      span {
        font-size: 2rem;
        font-weight: bold;
        margin: 0 0.5rem;
      }

      div {
        /* align-self: flex-start; */
        margin: 0 0.5rem;
        text-decoration: underline;
        /* justify-self: flex-start; */
      }
    }

    button {
      margin: 1.5rem;
      min-width: 3rem !important;
      width: 3rem !important;
      height: 3rem !important;
      color: white;
      border-radius: 50%;
    }
  }

  .divider {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 1px;

    span {
      width: 98%;
      background-color: var(--mui-palette-secondary-main);
    }
  }

  .content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 90%;

    .listContainer {
      width: 100%;
      height: 100%;
      border-radius: 1rem;
      margin: .5rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .listHeader {
        /* font-weight: bold; */
        height: fit-content;
        padding: 1rem;
        border-radius: 1rem;
        color: white;
        font-size: 2rem;
        display: flex;
        flex-direction: column;
        background-color: rgba(0, 0, 0, 0.4);

        span {
          font-size: 1.5rem;
        }

        i {
          padding: 0;
          font-weight: bold;
          margin: 0 0 1rem 0;
        }

        .buttonsContainer {
          width: 100%;
          display: flex;
          justify-content: space-between;
          margin: 1rem 0 0 0;

          button {
            width: fit-content;
            border-radius: 1rem;
            box-shadow: 0 0 1rem var(--mui-palette-primary-dark);
            padding: .5rem 1.2rem;
            font-size: 1.5rem;
            font-weight: bold;
            margin: 0 1rem;

            svg {
              font-size: 1.8rem;
            }
          }
        }
      }

      .list {
        overflow: auto;
        height: calc(100% - 5rem);
        margin: .5rem 0 .5rem 0;
        border-radius: 1rem;
        color: white;
        font-size: 2rem;
        display: flex;
        flex-direction: column;
        background-color: rgba(0, 0, 0, 0.4);

        .slot {
          table-layout: auto;
          margin: 1rem;
          border: 1px solid var(--mui-palette-secondary-main);
          /* width: 100%; */
          border-collapse: collapse;
          height: 100%;
          
          tr {
            border-bottom: 1px dotted var(--mui-palette-secondary-main);

            th {
              font-size: 1.2rem;
              background-color: var(--mui-palette-primary-dark);
              padding: .2rem;
            }

            td {
              padding: .2rem;
              font-size: 1rem;
              text-align: center;
            }
          }

          tr:hover > td {
            background-color: var(--mui-palette-secondary-dark);
          }
        }
      }

      ::-webkit-scrollbar-thumb {
        background-color: var(--mui-palette-primary-main);
        border-radius: 0 1rem 1rem 0;
      }
    }

    .rightColumn {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      margin: .5rem .5rem 1rem 0;
      width: 30%;
      height: calc(100% - .5rem);
      border-radius: 1rem;
      background-color: rgba(0, 0, 0, 0.4);

      .rightContainer {
        position: relative;
        display: flex;
        flex-direction: column;
        color: white;
        padding: 1rem;
        background-color: rgba(80, 80, 80, 0.3);
        margin: 1rem;
        border-radius: 1rem;
        overflow: hidden;

        i {
          margin: 1rem 0 0 0;
          font-size: 1.5rem;
          font-weight: bold;
        }

        .icon {
          top: -3rem;
          right: 0;
          position: absolute;
          font-size: 12rem;
          margin: 0;
          transform: rotate(15deg);
          z-index: 0;
          opacity: 0.3;
        }

        button {
          font-weight: bold;
          padding: 0 .5rem;
          font-size: 1.8rem;
        }
      }
    }
  }
}

.modalContainer {
  top: 0;
  left: 0;
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;

  .modalBG {
    padding: 1rem 5rem;
    width: fit-content;
    height: fit-content;
    background-color: rgba(80, 80, 80, 0.9);
    box-shadow: 0 0 1rem var(--mui-palette-secondary-light);
    border-radius: 1rem;
    color: white;
    text-align: center;
    display: flex;
    flex-direction: column;
    border: 1px solid var(--mui-palette-secondary-main);

    .modalDescription {
      font-size: 1.5rem;
      /* margin: 1rem; */
      margin: 0 0 1.2rem 0;
    }

    p {
      margin: 1rem;
      /* color: var(--mui-palette-primary-main); */
      color: white;
    }

    h2 {
      font-size: 2rem;
      margin: 1rem;
    }

    input {
      color: var(--mui-palette-primary-main);
      font-weight: bold;
      /* border: 1px solid var(--mui-palette-primary-main); */
      border-radius: .3rem;
      /* border: white 1px solid; */
    }

    button {
      margin: 1rem;
      font-weight: bold;
      padding: .5rem 1rem;
      font-size: 1.2rem;

      svg {
        font-size: 1.6rem;
        font-weight: bold;
      }
    }
  }
}